import request from '@/utils/request'

// 获取用户信息
export function getUserinfo(user_id) {
  return new Promise(resolve => {
    request({
      url: 'homeList',
      method: 'POST',
      data: {
        user_id
      }
    }).then(res => {
      resolve(res.data.user_info)
    })
  })
}

// 获取打卡信息
export function getDakainfo(user_id, organ_id) {
  return new Promise(resolve => {
    request({
      url: 'appkqgetpersondaydakamsg',
      method: 'POST',
      data: {
        user_id,
        organ_id
      }
    }).then(res => {
      resolve(res.data)
    })
  })
}
