export const dakaResultDic = [
  { id: 1, title: '正常' },
  { id: 2, title: '迟到' },
  { id: 3, title: '矿工' },
  { id: 4, title: '早退' },
  { id: 5, title: '缺卡' },
  { id: 6, title: '矿工' },
  { id: 8, title: '加班' },
  { id: 9, title: '当天无需打卡' }
]

export function getDakaResultById(id = null) {
  if (id === null) return id
  return dakaResultDic.find(r => r.id === id)
}
