<template lang="pug">
  van-cell-group.user-list(v-if="userIds.length > 0" inset title="快捷选择")
    .item(
      v-for="userId in userIds"
      :key="userId"
      @click="changeUserid(userId)"
      :class="{ active: userId === activeUserid, loading: activeLoading === userId }"
    ) {{ userId }}
</template>

<script>
export default {
  data() {
    return {
      activeUserid: null,
      activeLoading: null
    }
  },
  computed: {
    userIds() {
      return this.$store.state.user.config
    }
  },
  mounted() {
    if (this.userIds.length > 0) {
      this.changeUserid(this.userIds[0])
    }
  },
  methods: {
    changeUserid(userId) {
      this.activeLoading = userId
      this.$store.dispatch('getUserInfo', userId).then(userInfo => {
        this.activeUserid = userId
        this.$emit('change', userInfo)
        this.activeLoading = null
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .user-list {
    display: flex;
    padding: 15px;
    overflow: auto;
    .item {
      display: flex;
      align-items: center;
      background: #e8e8e8;
      padding: 8px 16px;
      border-radius: 5px;
      color: #233;
      margin-right: 10px;
      border: 1px solid #e8e8e8;
      &.active {
        border-color: #1989fa;
      }
      &:last-child {
        margin-right: 0;
      }
      &.loading {
        animation: loading .5s infinite;
      }
    }
  }
  // 边框闪烁动画
  @keyframes loading {
    0%, 100% { border-color: #1989fa; }
    50% { border-color: #e8e8e8; }
  }
</style>
