<template lang="pug">
  .index
    van-empty(v-if="!alreadyConfig" description="初次使用请先设置用户ID")
      router-link(to="setting"): van-button(round type="info" style="width: 120px;") 去设置

    user-selecter(ref="userSelecter" @change="getDakaInfo")

    van-cell-group.user-content(v-if="dakaInfo" title="打卡信息" inset)
      .headimg {{ userInfo?.username.substring(1, 3) }}
      .info
        .name {{ userInfo?.username }}
        .address(v-if="dakaInfo.bancimsg.length === undefined") {{ dakaInfo.bancimsg.banciname }}
        .address(v-else) 休息日，无需打卡

    van-cell-group.daka-content(v-if="dakaInfo && dakaInfo.dakadetail.length > 0" inset style="margin-top: 15px;")
      .daka-info
        van-icon(style="margin-right: 4px;" name="underway" color="#888")
        span.label(style="margin-right: 4px;") 已打卡
        span.yes {{ dakaInfo.dakadetail.filter(r => r.daka_result === 1).length }}
        span.all /{{ dakaInfo.dakadetail.length }}
      .banci-list
        div(:class="{ banci: true, active: allowDakaRegion?.data.bancimsg_id === dakadetail.bancimsg_id }" v-for="(dakadetail, i) in dakaInfo.dakadetail" :key="i")
          .title 上班 {{ dayjs(dakadetail.kaoqin_time * 1000).format('HH:mm') }}
          .result
            van-icon(v-if="dakadetail.daka_result === 1" name="checked" color="#1989fa" style="margin-right: 4px;")
            span(v-if="dakadetail.daka_result === 1" style="margin-right: 4px;") {{ dayjs(dakadetail.daka_time * 1000).format('HH:mm') }}
            span {{ getDakaResultById(dakadetail.daka_result)?.title }}
      .daka(v-if="allowDakaRegion !== undefined" @click="daka(userInfo.id)")
        .title 上班打卡
        .time {{ now }}
      .daka.enalbed(v-else)
        .title 未到时间
        .time {{ now }}

    van-cell-group(v-if="dakaInfo && dakaInfo.bancimsg.length === undefined" inset :title="`班次信息`")
      van-collapse(v-model="activeNames")
        van-collapse-item(title="迟到规则" name="迟到规则")
          div 迟到 {{ dakaInfo.bancimsg.chidaotime }}分钟 及以上视为迟到
          div 迟到 {{ dakaInfo.bancimsg.kuanggongtime }}分钟 及以上视为矿工
        van-collapse-item(title="上下班时间" name="上下班时间")
          div(v-for="(timemsg, i) in dakaInfo.bancimsg.timemsg" :key="'a' + i")
            div {{ timemsg.start_time }} - {{ timemsg.end_time }}
            div 上班前{{ timemsg.sbqian }}分钟内可打卡，后{{ timemsg.sbhou }}分钟内可打卡
            div 下班前{{ timemsg.xbqian }}分钟内可打卡，后{{ timemsg.xbhou }}分钟内可打卡
</template>

<script>
import request from '@/utils/request'
import dayjs from 'dayjs'
import { getDakaResultById } from '@/services/direction'
import { getDakainfo } from '@/services/daka'
import UserSelecter from '@/components/UserSelecter'

export default {
  name: 'Home',
  components: {
    UserSelecter
  },
  data() {
    return {
      dakaInfo: null,
      userInfo: null,
      now: '',
      activeNames: [],
      alreadyConfig: false
    }
  },
  computed: {
    allowDakaRegion() {
      const regions = this.getAllowDakaRegions()
      const find = regions.find(({ region }) => {
        return dayjs().unix() >= region[0].unix() && dayjs().unix() <= region[1].unix()
      })
      return find
    }
  },
  methods: {
    dayjs,
    getDakaResultById,
    getDakaInfo(userInfo) {
      this.userInfo = userInfo
      getDakainfo(userInfo.id, userInfo.organ_id).then(dakaInfo => {
        this.dakaInfo = dakaInfo
      })
    },
    daka(userid) {
      if (!this.allowDakaRegion) return
      const bancimsg_id = this.allowDakaRegion.data.bancimsg_id
      const data = {
        user_id: userid,
        kaoqin_id: this.dakaInfo.kaoqin_id,
        bancimsg_id,
        banci_id: this.dakaInfo.bancimsg.banci_id,
        dkfs_id: 0, // 打卡wifi(始终为0未启用)
        daka_name: '',
        is_waiqin: 0, // 当前为0 1外勤 2正常
        waiqin_img: '',
        waiqin_location: '',
        beizhu: ''
      }
      request({
        url: 'appkqdaka',
        method: 'POST',
        data
      }).then(res => {
        if (res.code !== 0) {
          this.$notify({ type: 'success', message: res.msg })
        }
        this.getDakaInfo(this.userInfo)
      })
    },
    getAllowDakaRegions() {
      const regions = []
      if (this.dakaInfo.bancimsg.length === 0) {
        return []
      }
      const timemsgs = this.dakaInfo.bancimsg.timemsg
      timemsgs.forEach(timemsg => {
        const startTime = dayjs(dayjs().format('YYYY/MM/DD') + ' ' + timemsg.start_time)
        regions.push({
          data: timemsg,
          region: [
            startTime.subtract(parseInt(timemsg.sbqian), 'minute'),
            startTime.add(parseInt(timemsg.sbhou), 'minute')
          ]
        })
      })
      return regions
    }
  },
  mounted() {
    if (this.$refs.userSelecter.userIds.length > 0) {
      this.alreadyConfig = true
    }

    this.now = dayjs().format('HH:mm:ss')
    setInterval(() => {
      this.now = dayjs().format('HH:mm:ss')
    }, 1000)
  }
}
</script>

