import { getUserinfo } from '@/services/daka'

const getDefaultState = () => {
  // 读取本地存储的用户信息
  let config = []
  try {
    const userIds = JSON.parse(localStorage.getItem('config')).userIds
    if (userIds.length > 0) config = userIds
  } catch {
    localStorage.removeItem('config')
  }
  return {
    config,
    userInfos: []
  }
}

const state = getDefaultState()

const getters = {
  config: state => state.config,
  userInfos: state => state.userInfos
}

const mutations = {
  SET_USERINFO: (state, userInfo) => {
    // 已经存在，则替换
    const index = state.userInfos.findIndex(r => r.id === userInfo.id)
    if (index > -1) {
      state.userInfos.splice(index, 1, userInfo)
      return
    }
    // 不存在，则添加
    state.userInfos.push(userInfo)
  }
}

const actions = {
  getUserInfo({ commit, state }, userId) {
    return new Promise(resolve => {
      // 如果用户信息已经存在，则直接返回
      const userInfo = state.userInfos.find(r => r.id.toString() === userId)
      if (userInfo) {
        resolve(userInfo)
        return
      }
      // 否则，从服务器获取用户信息
      getUserinfo(userId).then(userInfo => {
        commit('SET_USERINFO', userInfo)
        resolve(userInfo)
      })
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
