import axios from 'axios'
import { Notify } from 'vant'

const baseUrl = 'https://api.jinshengyun.cn/'

export default function({ method, url, data }) {
	return new Promise((resolve, reject) => {
		axios({
			method,
			url: baseUrl + url,
			data
		}).then(res => {
			resolve(res.data)
		}).catch(err => {
			Notify({ type: 'danger', message: err })
			reject(err)
		})
	})
}
