import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/statistics',
    name: 'Statistics',
    component: () => import('@/views/Statistics')
  },
  {
    path: '/setting',
    name: 'Setting',
    component: () => import('@/views/Setting')
  }
]

const router = new VueRouter({
  routes
})

export default router
